* {
    
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    height: 100%;
    overflow: hidden;
}
#root {
    width: 100vw;
    height: 100vh;
    /* background-color: rgb(24, 32, 44); */
}

.filename {
    text-align: center;
    overflow-x: clip;
    text-overflow: ellipsis;

    overflow-x: visible;
    /* text-overflow: none; */
}

.filename-shower {
    position: relative;
    /* s */
    left: -25%;
    z-index: 5;
}

p, h3, span {
    cursor: pointer;
}

.fg-light-primary {
    color: #f8fafc;
}
.fg-dark-primary {
    color: #1e293b;
}
.fg-light-secondary {
    color: #94a3b8;
}
.fg-dark-secondary {
    color: #e2e8f0;
}
.fg-white {
    color: #ffffff;
}
.fg-black {
    color: #000000;
}

.w-90 {
    width: 90%;
}



#loading {
    
    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
    animation: loader 1s infinite;
}

@keyframes loader {
    from {
        /* background-color: red; */
        rotate: 0deg;
    }

    to {
        /* background-color: gray; */
        rotate: 360deg;
    }
}